@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Tajawal:wght@200;300;400;500;700;800;900&display=swap");
@import url("https://db.onlinewebfonts.com/c/24795d84c015a4e8895cd995ef838852?family=KFGQPC+HAFS+Uthmanic+Script");

* {
  font-family: "Tajawal", sans-serif;
}
:root {
  --bg-color: #f1f5f9;
  --white-color: #fff;
  --header-color: #26ab7b;
  --main-color: #00d084;
  --sec-color: #40c491;
  --btn-color: #008655;
  --sidebar-color: #02b473;
  --inp-color: #aed581;
  --gray-color: #f7f7f7;
}
::selection {
  color: white;
  background-color: var(--main-color);
}
::-webkit-scrollbar {
  display: none;
}
body::-webkit-scrollbar-thumb {
  background-color: var(--sidebar-color);
  box-shadow: 0 0 30px 30px var(--sidebar-color);
}
/******** Start Sidebar ********/
aside {
  width: 270px;
  height: calc(100vh - 110px);
  &.aside-show {
    transform: translateX(102%);
  }
  ul li a::before {
    width: 5px;
  }
}
/******** End Sidebar ********/
/******** Start Boxes ********/
.main-h2::before {
  height: 3px;
  animation: scale 3s infinite;
}
@keyframes scale {
  0% {
    width: 35%;
  }
  50% {
    width: 100%;
  }
  100% {
    width: 35%;
  }
}
/******** End Boxes ********/
/******** Start Left ********/
.border-1 {
  border-width: 1px;
}
.time-pray {
  &:last-child {
    border-left: none;
  }
  &:not(:first-child) {
    padding: 0 10px 0 10px;
  }
}
.btm-fire .main-btn {
  width: 100%;
}
.show {
  width: 100px;
}

.listen .main-btn {
  margin: 0;
  padding-left: 21px;
  padding-right: 21px;
}
/******** End Left ********/
/******** Start Quran Page ********/
.quran-table {
  min-width: 1000px;
}
.left-quran {
  width: 800px;
}
hr {
  border: 1px solid var(--main-color);
}
/******** End Quran Page ********/
/******** Start Read Page ********/
.center-hadis {
  background-color: var(--white-color);
  border-radius: 6px;
  padding: 20px;
  overflow-y: scroll;
  width: -webkit-fill-available;
}
.left-listen {
  max-width: 500px;
  .main-btn {
    margin: 0;
    width: 120px;
  }
}
.topic * {
  font-family: "KFGQPC HAFS Uthmanic Script";
}
.page-num {
  background-image: url(../imgs/star.png);
  font-weight: bold !important;
}
.part {
  font-size: 20px;
  font-weight: bold;
}
.choose-sura {
  border-bottom: 1px solid var(--main-color);
}
.sura {
  // font-size: 34px;
  // line-height: 2;
  margin: 8px 0;
  text-align: center;
  font-family: "conv_original-hafs";
  white-space: normal;
  div {
    background: url(../imgs/Aya-End.png);
    background-size: cover;
    height: 40px;
    width: 40px;
    font-size: 16px;
    display: inline-grid;
    place-content: center;
    margin-left: 5px;
  }
}
/******** End Read Page ********/
/******** Start Login Page ********/
.page-2 {
  background-image: url("../imgs/mosque.png");
}
.right-login {
  width: 70%;
  position: relative;
  background-color: var(--white-color);
}
.content-login {
  padding: 0;
}
.top-login h2 {
  display: flex;
  margin: 15px auto 0;
  font-size: 30px;
}
.login-form {
  display: flex;
  flex-direction: column;
  width: 450px;
  gap: 20px;
  margin: 20px auto 0px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.login-form input {
  border: none;
  padding: 15px;
  font-weight: bold;
  font-size: 15px;
  /* color: white; */
  border-radius: 4px;
  background-color: var(--bg-color);
}
.login-form input::placeholder {
  color: #222;
}
form button {
  border: none;
  cursor: pointer;
}
.forget-pass {
  text-align: center;
  color: #222;
  font-weight: bold;
}
/******** End Login Page ********/
/******** Start Books Page ********/
.center-books,
.center-azkar {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
}
.center-books .box .main-btn {
  width: 90px;
}
/******** End Books Page ********/
/******** Start Azkar Page ********/
.center-azkar .box {
  .main-btn {
    width: 90px;
  }
  &::before {
    border-radius: 50%;
  }
  &:hover {
    &::before {
      border-radius: 0;
    }
    p,
    h2 {
      color: var(--white-color);
    }
  }
}
.btm-zkr .zkrs {
  font-size: 21px;
}
/******** End Azkar Page ********/
.main-btn {
  --clr-font-main: hsla(0 0% 20% / 100);
  --btn-bg-1: var(--btn-color);
  --btn-bg-2: var(--sidebar-color);
  --btn-bg-color: hsla(360 100% 100% / 1);
  --radii: 0.5em;
  background-size: 280% auto;
  background-image: linear-gradient(
    325deg,
    var(--btn-bg-2) 0%,
    var(--btn-bg-1) 55%,
    var(--btn-bg-2) 90%
  );
}
.main-btn:hover {
  background-position: right top;
}
.main-btn:is(:focus, :focus-within, :active) {
  outline: none;
  box-shadow: 0 0 0 3px var(--btn-bg-color), 0 0 0 6px var(--btn-bg-2);
}
@media (prefers-reduced-motion: reduce) {
  .btn-donate {
    transition: linear;
  }
}
/******** Start hadis Page ********/
.hadis:not(:first-child) {
  margin-top: 25px;
}
/******** End hadis Page ********/
/******** Start pray Page ********/
.copy-btn {
  background-color: #13a95f;
}
/******** End pray Page ********/
/******** Start Listen Quran Page ********/
.quran-table {
  outline: 2px solid #e5e5e5;
  tbody tr {
    border-bottom: 2px solid #e5e5e5;
    &:last-of-type {
      border: none;
    }
    &:hover {
      background-color: #eee;
    }
  }
  td:not(:last-child) {
    border-left: 2px solid #e5e5e5;
  }
  .main-btn {
    width: fit-content;
  }
}
.lis {
  a,
  button {
    margin: 0;
  }
}
/******** End Listen Quran Page ********/
/******** Start Responsive ********/
@media (width <= 1244px) {
  .center-pray {
    flex-direction: column;
    gap: 10px;
    border-bottom: 0;
  }
  .time-pray {
    border: 0;
    border-bottom: 1px solid var(--main-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding-right: 0.75rem !important;
    > * {
      margin-bottom: 14px;
    }
    &::before {
      content: ":";
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 5px;
      font-size: 18px;
      font-weight: bold;
    }
  }
}
@media (width <= 890px) {
  .content {
    flex-direction: column-reverse;
    height: auto;
  }
  aside {
    width: calc(100% - 3rem);
    &.aside-show {
      transform: translateX(101%);
    }
  }
  .left-listen {
    max-width: 100%;
  }
}
@media (width <= 444px) {
  .left-listen .main-btn {
    width: 90px;
  }
  .center-listen .topic h2 {
    font-size: 26px !important;
  }
}
/******** End Responsive ********/
/******** Start 404 Error ********/
.imgError {
  width: 600px;
  animation: move 3s infinite;
}
@keyframes move {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-25px);
  }
  100% {
    transform: translateY(0);
  }
}
/******** End 404 Error ********/
.center-mobile {
  height: 100vh;
  flex-direction: row;
}
